<template>
	<div class="main">
		<div class="bread-crumbs columnCenterStart">
			<el-breadcrumb separator-class="el-icon-arrow-right">
				<el-breadcrumb-item :to="{ path: 'userOrderAdmin' }"></el-breadcrumb-item>
			  <el-breadcrumb-item></el-breadcrumb-item>
			</el-breadcrumb>
			<div class="now-position"></div>
		</div>
		<div class="table-area">
			<div class="info-tip"></div>
			<div class="rowStartCenter info-desc">
				<span>	{{baseInfo.orderNo}}</span>
				<span>
					<span v-if="baseInfo.orderStatus == '0'"></span>
					<span v-else-if="baseInfo.orderStatus == '1'"></span>
					<span v-else-if="baseInfo.orderStatus == '2'"></span>
					<span v-else-if="baseInfo.orderStatus == '3'"></span>
					<span v-else-if="baseInfo.orderStatus == '4'"></span>
					<span v-else-if="baseInfo.orderStatus == '5'"></span>
					<span v-else-if="baseInfo.orderStatus == '6'"></span>
					<span v-else-if="baseInfo.orderStatus == '7'"></span>
				</span>
				<!-- <span>{{baseInfo.phone}}</span> -->
				<span>{{baseInfo.totalPrice}}</span>
				<span>{{baseInfo.payTime}}</span>
				<!-- <span>{{baseInfo.phone}}</span> -->
				<span>{{baseInfo.createTime}}</span>
				<span>
					<span v-if="baseInfo.payStatus == '0'"></span>
					<span v-else-if="baseInfo.payStatus == '1'"></span>
				</span>
				<!-- <span>	{{baseInfo.phone}}</span> -->
				<span>{{baseInfo.sendTime}}</span>
			</div>
			<div class="info-tip"></div>
			<div class="rowStartCenter info-desc">
				<span>	{{baseInfo.addressInfo.receiver}}</span>
				<span>{{baseInfo.addressInfo.phone}}</span>
				<!-- <span>{{baseInfo.phone}}</span> -->
				<span>{{baseInfo.addressInfo.area}}{{baseInfo.addressInfo.addressDetail}}{{baseInfo.addressInfo.numbers}}</span>
			</div>
			<!-- <div class="info-tip"></div>
			<div class="rowStartCenter info-desc">
				<span>{{baseInfo.nickName}}</span>
				<span>{{baseInfo.phone}}</span>
			</div>
			<div class="info-tip"></div>
			<div class="rowStartCenter info-desc">
				<span>{{baseInfo.nickName}}</span>
				<span>{{baseInfo.phone}}</span>
			</div> -->
			<div class="info-tip"></div>
			<el-table 
			 :data="baseInfo.orderDetails"
			 border 
			 class="my-table"
			 :header-cell-style="{background:'#F5F6FA',color:'#000',fontWeight:'bold'}">
				<el-table-column prop="productsName" label="" algin="center"></el-table-column>
				<el-table-column prop="majorPicture" label="" algin="center">
					<template slot-scope="scope">
						<img class="good-pic" :src="scope.row.majorPicture" alt="">
					</template>
				</el-table-column>
				<el-table-column prop="area" label="" algin="center">
					<template slot-scope="scope">
						<span v-for="list in scope.row.attrOptContent">{{list.optionName}};</span>
					</template>
				</el-table-column>
				<!-- <el-table-column prop="area" label="" algin="center"></el-table-column> -->
				<el-table-column prop="number" label="" algin="center"></el-table-column>
				<el-table-column prop="price" label="" algin="center"></el-table-column>
				<el-table-column prop="introduction" label="" algin="center" :show-overflow-tooltip="true"></el-table-column>
			</el-table>
		</div>
	</div>
</template>

<script>
	export default {
		data() {
			return {
				orderId: this.$route.query.orderId,
				baseInfo: '',
			}
		},
		mounted() {
			this.getDesc();
		},
		methods: {
			getDesc() {
				let apiurl = this.api.getOrderDetailById+'?orderId='+this.orderId;
				this.common.getAxios(apiurl, this.returnDesc);
			},
			returnDesc(res) {
				if(res.data.status) {
					this.baseInfo = res.data.data;
					this.baseInfo.addressInfo = JSON.parse(this.baseInfo.addressInfo);
					for(var i in this.baseInfo.orderDetails) {
						this.baseInfo.orderDetails[i].attrOptContent = JSON.parse(this.baseInfo.orderDetails[i].attrOptContent);
					}
				} else {
					this.$message.error(res.data.msg);
				}
			}
		}
	}
</script>

<style scoped>
	.info-tip{
		color: #333;
		font-size: 1.4rem;
		padding-left: 1rem;
		border-left: 0.4rem solid #F44A00;
		line-height: 2.2rem;
		text-align: left;
	}
	.info-desc{
		font-size: 1.4rem;
		margin: 2rem 0 2rem 2rem;
		flex-wrap: wrap;
	}
	.info-desc span{
		margin-right: 3rem;
	}
	.good-pic{
		width: 3rem;
		height: 3rem;
	}
	.my-table{
		text-align: center;
	}
</style>



