<template>
	<div class="main">
		<div class="bread-crumbs columnCenterStart">
			<el-breadcrumb separator-class="el-icon-arrow-right">
			  <el-breadcrumb-item>/</el-breadcrumb-item>
			</el-breadcrumb>
			<div class="now-position"></div>
		</div>
		<div class="table-area">
			<div class="search-area rowBetweenCenter">
				<div class="rowStartCenter search-input-area">
					<!-- <span class="input-tip"></span>
					<el-input v-model="searchForm.nickName" clearable class="search-input"></el-input> -->
				</div>
				<div class="btn-area rowBetweenCenter">
					<el-button type="primary" @click="addTable"></el-button>
					<!-- <el-button type="primary" @click="getTable" plain></el-button> -->
				</div>
			</div>
			<el-table 
			 :data="tableData" 
			 border 
			 class="my-table"
			 :header-cell-style="{background:'#F5F6FA',color:'#000',fontWeight:'bold'}">
				<el-table-column type="index" :index="indexMethod" label="" width="100" align="center">
				</el-table-column>
				<el-table-column label="" prop="couponDescribe" align="center" :show-overflow-tooltip="true"></el-table-column>
				<el-table-column label="" prop="startTime" align="center"></el-table-column>
				<el-table-column label="" prop="endTime" align="center"></el-table-column>
				<el-table-column label="" prop="number" align="center"></el-table-column>
				<el-table-column label="" prop="useStart" align="center"></el-table-column>
				<el-table-column label="" prop="useEnd" align="center"></el-table-column>
				<el-table-column label="" prop="createTime" align="center" :show-overflow-tooltip="true"></el-table-column>
				<el-table-column label="" align="center" min-width="120px">
					<template slot-scope="scope">
						<el-button
							type="text"
							size="mini">
							<span @click="viewTable(scope.$index, scope.row)" class="text-black"></span>
						</el-button>
						<!-- <el-button
							 v-if="scope.row.backUserSign!='1'"
							type="text"
							size="mini">
							<span @click="delTable(scope.$index, scope.row, '1')" class="text-red"></span>
						</el-button> -->
					</template>
				</el-table-column>
			</el-table>
		</div>
	</div>
</template>

<script>
	export default {
		data() {
			return {
				page: 1,
				length: 10,
				total: 0,
				tableData: [],
				// form
				searchForm: {
					nickName: '',
					phone: ''
				},
				formLabelWidth: '80px',
				dialogFormVisible: false, //false
			}
		},
		mounted() {
			this.getTable();
		},
		methods: {
			// 
			indexMethod(index) {
			  return (this.page - 1) * 10 + index + 1;
			},
			// table
			initTable() {
				this.page = 1;
				this.getTable();
			},
			// table
			getTable() {
				let apiurl = this.api.selectCoupon+'?page='+this.page+'&length='+this.length;
				this.common.getAxios(apiurl, this.returnTable);
			},
			// table
			returnTable(res) {
				this.tableData = res.data.data;
				// this.total = res.data.data.total;
			},
			handleCurrentChange(val) {
				this.page = val;
				this.getTable();
			},
			// table
			viewTable(index, row) {
				console.log(index, row);
				this.$router.push({name: 'couponAdminDesc', params: {
					descType: 'edit',
					id: row.id,
				}})
			},
			addTable() {
				this.$router.push({name: 'couponAdminDesc', params: {descType: 'add'}})
			},
			// table
			delTable(index, row) {
				let _this = this;
				this.$confirm(', ?', '', {
					confirmButtonText: '',
					cancelButtonText: '',
					type: 'warning'
				}).then(() => {
					let apiurl = this.api.deleteBannerById;
					let banner = {
						id: row.id
					}
					this.common.deleteAxios(apiurl, {data: banner}, this.returnDel);
				}).catch(() => {
					_this.$message({
						type: 'info',
						message: ''
					});          
				});
			},
			returnDel(res) {
				if(res.data.status) {
					this.$message({
						type: 'success',
						message: ''
					});
					this.getTable();
				} else {
					this.$message.error(res.data.msg);
				}
			}
		}
	}
</script>

<style scoped>
	.user-dialog{
		width: 60%;
		margin: 0 auto;
	}
</style>



