<template>
	<div class="main">
		<div class="bread-crumbs columnCenterStart">
			<el-breadcrumb separator-class="el-icon-arrow-right">
				<el-breadcrumb-item :to="{ path: 'shopAdmin' }"></el-breadcrumb-item>
			  <el-breadcrumb-item></el-breadcrumb-item>
			</el-breadcrumb>
			<div class="now-position"></div>
		</div>
		<div class="table-area">
			<div class="info-tip"></div>
			<div class="rowStartCenter info-desc my-wrap">
				<span>{{baseInfo.company}}</span>
				<span>{{baseInfo.addressArea}}</span>
				<span>{{baseInfo.addressDetail}}</span>
				<span>{{baseInfo.contacts}}</span>
				<span>{{baseInfo.contactsPhone}}</span>
				<span>{{baseInfo.email}}</span>
				<span>{{baseInfo.shopsScore}}</span>
				<span>
					<span v-if="baseInfo.authStatus == 0"></span>
					<span v-else-if="baseInfo.authStatus == 1"></span>
					<span v-else-if="baseInfo.authStatus == 2"></span>
				</span>
				
			</div>
			<div class="info-tip"></div>
			<div class="info-desc rowStartCenter">
				<img class="shop-img" :src="baseInfo.businessLicense">
			</div>
			<div class="info-tip"></div>
			<div class="rowStartCenter my-wrap info-desc">
				<img class="shop-img" :src="img.image" v-for="img in baseInfo.images">
			</div>
		</div>
		
	</div>
</template>

<script>
	export default {
		data() {
			return {
				shopId: this.$route.query.shopId,
				userId: this.$route.query.userId,
				baseInfo: {},
				addressList: [],
			}
		},
		mounted() {
			this.getInfo();
		},
		methods: {
			// 
			getInfo() {
				let apiurl = (`${this.api.getBackShopsInfo}?userId=${this.userId}&shopsId=${this.shopId}`);
				this.common.getAxios(apiurl, this.returnInfo);
			},
			returnInfo(res) {
				this.baseInfo = res.data.data;
				this.baseInfo.images = JSON.parse(this.baseInfo.iamges);
				console.log(this.baseInfo)
				// this.addressList = res.data.data.addressList;
			}
		}
	}
</script>

<style>
	.info-tip{
		color: #333;
		font-size: 1.4rem;
		padding-left: 1rem;
		border-left: 0.4rem solid #F44A00;
		line-height: 2.2rem;
		text-align: left;
	}
	.info-desc{
		font-size: 1.4rem;
		margin: 2rem 0 2rem 2rem;
	}
	.info-desc span{
		margin-right: 3rem;
		line-height: 2.4rem;
	}
	.shop-img{
		width: 18rem;
		margin-right: 2rem;
	}
</style>



