import Vue from 'vue'
import Router from 'vue-router'
import login from '@/components/login' //
import index from '@/components/index' //
import userAdmin from '@/components/userAdmin/userAdmin' //-
import userAdminDesc from '@/components/userAdmin/userAdminDesc' //--
import shopAdmin from '@/components/shopAdmin/shopAdmin' //-
import shopAdminDesc from '@/components/shopAdmin/shopAdminDesc' //--
import shopDuesAdmin from '@/components/orderAdmin/shopDuesAdmin' //-
import userOrderAdmin from '@/components/orderAdmin/userOrderAdmin' //-
import userOrderAdminDesc from '@/components/orderAdmin/userOrderAdminDesc' //-
import goodsAdmin from '@/components/goodsAdmin/goodsAdmin' //-
import goodsAdminDesc from '@/components/goodsAdmin/goodsAdminDesc' //-
import industryAdmin from '@/components/sortAdmin/industryAdmin' //-
import bannerAdmin from '@/components/sortAdmin/bannerAdmin' //-banner
import bannerAdminDesc from '@/components/sortAdmin/bannerAdminDesc' //-banner-banner
import attributesAdmin from '@/components/sortAdmin/attributesAdmin' //-
import couponAdmin from '@/components/otherAdmin/couponAdmin' //- 
import couponAdminDesc from '@/components/otherAdmin/couponAdminDesc' //--
import rightsProtection from '@/components/otherAdmin/rightsProtection' //- 
import refundAdmin from '@/components/otherAdmin/refundAdmin' //- 
import scoreAdmin from '@/components/otherAdmin/scoreAdmin' //- 
import agreementAdmin from '@/components/otherAdmin/agreementAdmin' //- 
import authorityAdmin from '@/components/authorityAdmin/authorityAdmin' //-
import roleAdmin from '@/components/authorityAdmin/roleAdmin' //-
import logAdmin from '@/components/authorityAdmin/logAdmin' //-
import backgroundUserAdmin from '@/components/authorityAdmin/backgroundUserAdmin' //-


Vue.use(Router)

export default new Router({
  routes: [
		{
		  path: '/',
		  name: 'login',
		  component: login,
			meta: {
				title: ''
			}
		},{
		  path: '/index',
		  name: 'index',
			redirect: 'index/userAdmin',
		  component: index,
			meta: {
				title: ''
			},
			children: [{
				path: 'userAdmin',
				component: userAdmin,
				meta: {
					title: ''
				}
			},{
				path: 'userAdminDesc',
				component: userAdminDesc,
				meta: {
					title: ''
				}
			},{
				path: 'shopAdmin',
				component: shopAdmin,
				meta: {
					title: ''
				}
			},{
				path: 'shopAdminDesc',
				component: shopAdminDesc,
				meta: {
					title: ''
				}
			},{
				path: 'shopDuesAdmin',
				component: shopDuesAdmin,
				meta: {
					title: ''
				}
			},{
				path: 'userOrderAdmin',
				component: userOrderAdmin,
				meta: {
					title: ''
				}
			},{
				path: 'userOrderAdminDesc',
				component: userOrderAdminDesc,
				meta: {
					title: ''
				}
			},{
				path: 'goodsAdmin',
				component: goodsAdmin,
				name: 'goodsAdmin',
				meta: {
					title: ''
				}
			},{
				path: 'goodsAdminDesc',
				component: goodsAdminDesc,
				name: 'goodsAdminDesc',
				meta: {
					title: ''
				}
			},{
				path: 'industryAdmin',
				component: industryAdmin,
				meta: {
					title: ''
				}
			},{
				path: 'bannerAdmin',
				component: bannerAdmin,
				meta: {
					title: 'banner'
				}
			},{
				path: 'bannerAdminDesc',
				name: 'bannerAdminDesc',
				component: bannerAdminDesc,
				meta: {
					title: 'banner'
				}
			},{
				path: 'attributesAdmin',
				component: attributesAdmin,
				meta: {
					title: ''
				}
			},{
				path: 'couponAdmin',
				component: couponAdmin,
				meta: {
					title: ''
				}
			},{
				path: 'couponAdminDesc',
				name: 'couponAdminDesc',
				component: couponAdminDesc,
				meta: {
					title: ''
				}
			},{
				path: 'rightsProtection',
				component: rightsProtection,
				meta: {
					title: ''
				}
			},{
				path: 'refundAdmin',
				component: refundAdmin,
				meta: {
					title: ''
				}
			},{
				path: 'agreementAdmin',
				component: agreementAdmin,
				meta: {
					title: ''
				}
			},{
				path: 'scoreAdmin',
				component: scoreAdmin,
				meta: {
					title: ''
				}
			},{
				path: 'authorityAdmin',
				component: authorityAdmin,
				meta: {
					title: ''
				}
			},{
				path: 'roleAdmin',
				component: roleAdmin,
				meta: {
					title: ''
				}
			},{
				path: 'logAdmin',
				component: logAdmin,
				meta: {
					title: ''
				}
			},{
				path: 'backgroundUserAdmin',
				component: backgroundUserAdmin,
				meta: {
					title: ''
				}
			}]
		},
    
  ]
})



