<template>
	<div class="main">
		<div class="bread-crumbs columnCenterStart">
			<el-breadcrumb separator-class="el-icon-arrow-right">
			  <el-breadcrumb-item></el-breadcrumb-item>
			</el-breadcrumb>
			<div class="now-position"></div>
		</div>
		<div class="table-area">
			<div class="search-area rowBetweenCenter">
				<div class="rowStartCenter search-input-area">
					<span class="input-tip"></span>
					<el-input v-model="searchForm.nickName" clearable class="search-input"></el-input>
					<span class="input-tip"></span>
					<el-input v-model="searchForm.phone" clearable class="search-input"></el-input>
				</div>
				<div class="btn-area">
					<el-button type="primary" @click="getTable"></el-button>
				</div>
			</div>
			<el-table 
			 :data="tableData" 
			 border 
			 class="my-table"
			 :header-cell-style="{background:'#F5F6FA',color:'#000',fontWeight:'bold'}">
				<el-table-column type="index" :index="indexMethod" label="" width="100" align="center">
				</el-table-column>
				<el-table-column label="" prop="nickName" align="center"></el-table-column>
				<el-table-column label="" align="center">
					<template slot-scope="scope">
						<img :src="scope.row.avatarUrl" style="width:2.4rem;height:2.4rem">
					</template>
				</el-table-column>
				<el-table-column label="" prop="phone" align="center"></el-table-column>
				<el-table-column label="" prop="nickName" align="center">
					<template slot-scope="scope">
						<span v-if="scope.row.shopsSign == '0'"></span>
						<span v-else-if="scope.row.shopsSign == '1'"></span>
					</template>
				</el-table-column>
				<el-table-column label="" prop="createTime" align="center"></el-table-column>
				<!-- <el-table-column label="" prop="nickName"></el-table-column> -->
				<el-table-column label="" align="center">
					<template slot-scope="scope">
						<el-button
							type="text"
							size="mini">
							<span @click="viewTable(scope.$index, scope.row)" class="text-black"></span>
						</el-button>
						<el-button
							 v-if="scope.row.backUserSign!='1'"
							type="text"
							size="mini">
							<span @click="setTable(scope.$index, scope.row)" class="text-red"></span>
							</el-button>
					</template>
				</el-table-column>
			</el-table>
			<div class="block rowEndCenter">
			  <el-pagination
			    layout="prev, pager, next"
			    :total="total"
					@current-change="handleCurrentChange">
			  </el-pagination>
			</div>
		</div>
		<!--  -->
		<el-dialog title="" :visible.sync="dialogFormVisible" :before-close="clearForm">
			<el-form :model="dialogForm" :rules="dialogFormRules" ref="dialogForm" class="dialogForm user-dialog">
				<el-form-item label=":" :label-width="formLabelWidth" prop="username">
					<el-input v-model="dialogForm.username" autocomplete="off"></el-input>
				</el-form-item>
				<el-form-item label=":" :label-width="formLabelWidth" prop="password">
					<el-input v-model="dialogForm.password" autocomplete="off" show-password></el-input>
				</el-form-item>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click="clearForm"> </el-button>
				<el-button type="primary" @click="saveSet('dialogForm')"> </el-button>
			</div>
		</el-dialog>
	</div>
</template>

<script>
	export default {
		data() {
			return {
				page: 1,
				length: 10,
				total: 0,
				tableData: [],
				// form
				searchForm: {
					nickName: '',
					phone: ''
				},
				// 
				dialogForm: {
					username: '',
					password: ''
				},
				dialogFormRules: {
					username: [
						{ required: true, message: '', trigger: 'blur' },
					],
					password: [
						{ required: true, message: '', trigger: 'blur' },
					],
				},
				formLabelWidth: '80px',
				dialogFormVisible: false, //false
			}
		},
		mounted() {
			this.initTable();
		},
		methods: {
			// 
			indexMethod(index) {
			  return (this.page - 1) * 10 + index + 1;
			},
			// table
			initTable() {
				this.page = 1;
				this.getTable();
			},
			// table
			getTable() {
				let apiurl = this.api.listUser + '?page=' + this.page + '&length=' + this.length;
				if(this.searchForm.nickName != '') {
					apiurl += '&nickName=' + this.searchForm.nickName;
				}
				if(this.searchForm.phone != '') {
					apiurl += '&phone=' + this.searchForm.phone;
				}
				this.common.getAxios(apiurl, this.returnTable);
			},
			// table
			returnTable(res) {
				this.tableData = res.data.data.records;
				this.total = res.data.data.total;
			},
			handleCurrentChange(val) {
				this.page = val;
				this.getTable();
			},
			// table
			viewTable(index, row) {
				console.log(index, row);
				this.$router.push({path: 'userAdmindesc', query: {userId: row.id}})
			},
			// 
			setTable(index, row) {
				this.userId = row.id;
				this.dialogFormVisible = true;
			},
			// 
			saveSet(formName) {
				let _this = this;
				this.$refs[formName].validate((valid) => {
					if (valid) {
						let apiurl = _this.api.insertBackgroudUser;
						let backgroudUser = {
							backgroudUserName: _this.dialogForm.username,
							bcakgroudUserPassword: _this.dialogForm.password,
							userId: _this.userId,
							roleId: 2
						};
						_this.common.postAxios(apiurl, backgroudUser, _this.returnSave)
					} else {
						return false;
					}
				});
			},
			returnSave(res) {
				let _this = this;
				if(res.data.status) {
					_this.$message({
						type: 'success',
						message: ''
					});
					_this.$refs['dialogForm'].resetFields();
					_this.dialogFormVisible = false;
          _this.initTable();
				} else {
					_this.$refs['dialogForm'].resetFields();
					_this.$message.error(res.data.msg);
					// _this.dialogFormVisible = false;
					_this.dialogForm = {
						username: '',
						password: ''
					}
				}
			},
			// 
			clearForm() {
				let _this = this;
				_this.$refs['dialogForm'].resetFields();
				_this.dialogFormVisible = false;
			},
		}
	}
</script>

<style scoped>
	.user-dialog{
		width: 60%;
		margin: 0 auto;
	}
</style>



